import React, { Component } from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';

import './Tech.css';

class Tech extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Storage: {
        FrontEnd: [
          'JavaScript',
          'React',
          'Socket.IO',
          'HTML5',
          'CSS3',
          'React Redux',
          'jQuery',
          'Vue',
          'AngularJS',
          'React Native'
        ],
        BackEnd: [
          'Node.js',
          'Express',
          'MySQL',
          'PostgreSQL',
          'MongoDB',
          'RESTful API',
          'bcrypt',
          'Passport.js',
        ],
        DevTools: [
          'Git',
          'NPM',
          'Webpack',
          'Yarn',
          'Babel',
          'TDD',
          'Heroku',
          'AWS',
        ],
      },
      FrontEnd: [],
      BackEnd: [],
      DevTools: [],
    }
  }

  componentDidMount() {
    const { Storage } = this.state;
    let delay = 0;
    
    for (let i = 0; i < Storage.FrontEnd.length; i++) {
      setTimeout(() => this.setState({ FrontEnd: [...this.state.FrontEnd, Storage.FrontEnd[i]]}), delay);
      if (i < 8) {
        setTimeout(() => this.setState({ BackEnd: [...this.state.BackEnd, Storage.BackEnd[i]]}), delay);
      }
      if (i < 8) {
        setTimeout(() => this.setState({ DevTools: [...this.state.DevTools, Storage.DevTools[i]]}), delay)
      }
      delay += 500;
    }
  }

  render() {
    return (
        <div className="midTechContainer">
          <div>
            <h2>Tech Stack</h2>
            <div className="cover">
              {Object.keys(this.state).map((techType, index) => {
                if (index > 0) {
                  return <div className="techType" key={index}>
                    <div className="techTypeTitle">
                      <h3>{techType}</h3>
                    </div>
                    <div className="techStack">
                      <ReactCSSTransitionGroup
                        className="techAnimation"
                        component='div'
                        transitionName="techAnimation"
                        transitionAppear={true}
                        transitionAppearTimeout={2000}
                        transitionEnter={true}
                        transitionEnterTimeout={2000}
                        transitionLeave={false}>
                        {this.state[techType].map((tech, index) =>
                          <div className="eachTech" key={index}>
                            <div id="octagon">
                              {tech}
                            </div>
                          </div>
                        )}
                      </ReactCSSTransitionGroup>
                    </div>
                  </div>
                } else {
                  return false
                }
              })}
            </div>
          </div>
        </div>
    )
  }
}

export default Tech;


